<template>
  <div class="main_navs">
    <Header></Header>
    <section class="prof">
      <div class="container">
        <h1 class="h1_main">
          <span>{{$t('tit1')}}</span><br>
          {{$t('tit2')}}
          <span>{{$t('tit3')}}</span>
        </h1>
        <div class="container mb-4">

          <div class="start_test_c" v-if="$store.state.tests">
            <div class="start_name">{{$store.state.tests[0].title}}</div>
            <div class="start_desc" v-if="$store.state.tests">
              {{$store.state.tests[0].description}}
            </div>

            <div class="start_btn" v-if="$store.state.tests">
              <a href="#" @click="oneTest($store.state.tests[0].id)">
                <div>
                  {{$t('alert_btn')}}
                </div>
              </a>
            </div>

          </div>
          <div class="row">
            <div class="col-xl-6" v-if="$store.state.tests">
              <div class="start_card">
                <div class="start_c_titile" v-if="$store.state.tests">
                  {{$store.state.tests[1].title}}
                </div>
                <div class="start_test_desc" v-if="$store.state.tests">
                  {{$store.state.tests[1].description}}
                </div>

                <div class="start_btn test_link">
                  <a href="#" @click="twoTest($store.state.tests[1].id)">
                    <div>
                      {{$t('alert_btn')}}
                    </div>
                  </a>
                </div>
              </div>
            </div>
            <div class="col-xl-6" v-if="$store.state.tests">
            <div class="start_card">
              <div class="start_c_titile" v-if="$store.state.tests">
                {{$store.state.tests[2].title}}
              </div>
              <div class="start_test_desc" v-if="$store.state.tests">
                {{$store.state.tests[2].description}}
              </div>

              <div class="start_btn test_link">
                <a href="#" @click="theree($store.state.tests[2].id)">
                  <div>
                    {{$t('alert_btn')}}
                  </div>
                </a>
              </div>
            </div>
          </div>

            <div class="col-xl-6" v-if="$store.state.tests">
              <div class="start_card">
                <div class="start_c_titile" v-if="$store.state.tests">
                  {{$store.state.tests[3].title}}
                </div>
                <div class="start_test_desc" v-if="$store.state.tests">
                  {{$store.state.tests[3].description}}
                </div>

                <div class="start_btn test_link">
                  <a href="#" @click="four($store.state.tests[3].id)">
                    <div>
                      {{$t('alert_btn')}}
                    </div>
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";

export default {
  components: {Footer, Header},
  data() {
    return {
      options: [
        {value: '1', text: 'aa' + ' - ' + '1'},
        {value: '2', text: 'ab' + ' - ' + '2'},
        {value: '3', text: 'bc' + ' - ' + '3'},
        {value: '4', text: 'cd' + ' - ' + '4'},
        {value: '5', text: 'de' + ' - ' + '5'}
      ],
      item: {
        value: '',
        text: ''
      },
    }
  },
  mounted() {
    this.$store.dispatch('getProfision')
    this.$store.dispatch('getTests')
  },
  methods:{
    oneTest(item){
      this.$axiosDefault.get(`students/me/`,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
              'Accept-Language':'oz'
            }
          }
      )
          .then(()=>{
            this.$router.push(`/test/${item}/on-test`)
          })
          .catch(error=>{
            console.log(error.response.status)
            if(error.response.status==401){
              this.$toast(`${error.response.statusText}`, {
                timeout: 2000,
                type:"error"
              });
            }
            this.$router.push('/start-test')
          })
    },
    twoTest(item){
      this.$axiosDefault.get(`students/me/`,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
              'Accept-Language':'oz'
            }
          }
      )
          .then(()=>{
            this.$router.push(`/test/${item}/two-test`)
          })
          .catch(error=>{
            console.log(error.response.status)
            if(error.response.status==401){
              this.$toast(`${error.response.statusText}`, {
                timeout: 2000,
                type:"error"
              });
            }
            this.$router.push('/start-test')
          })
    },
    theree(item){
      this.$axiosDefault.get(`students/me/`,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
              'Accept-Language':'oz'
            }
          }
      )
          .then(()=>{
            this.$router.push(`/test/${item}/there-test`)
          })
          .catch(error=>{
            console.log(error.response.status)
            if(error.response.status==401){
              this.$toast(`${error.response.statusText}`, {
                timeout: 2000,
                type:"error"
              });
            }
            this.$router.push('/start-test')
          })
    },
    four(item){
      this.$axiosDefault.get(`students/me/`,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
              'Accept-Language':'oz'
            }
          }
      )
          .then(()=>{
            this.$router.push(`/test/${item}/four-test`)
          })
          .catch(error=>{
            console.log(error.response.status)
            if(error.response.status==401){
              this.$toast(`${error.response.statusText}`, {
                timeout: 2000,
                type:"error"
              });
            }
            this.$router.push('/start-test')
          })
    }


  }

}
</script>
